
#guests .note {
		margin: 5px;
		padding: 10px;
		background: var(--color-background-dark);
		border-radius: var(--border-radius);
		border: 1px solid var(--color-border);
}
#guests .whitelist {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
}
#guests .whitelist .multiselect {
			width: calc(100% - 48px);
			margin-right: 0;
}
#guests .whitelist .multiselect .multiselect__tags {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-right: 0;
				-ms-flex-wrap: nowrap;
				    flex-wrap: nowrap;
}
#guests .whitelist button {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			margin: 0;
}
#guests .whitelist .whitelist-toggle {
			margin: 1em 0;
}
