
#guests-list {
	padding: 20px 0 0;
}
#guests-list table {
		margin: 0 -30px;
		table-layout: fixed;
		width: calc(100% + 60px);
}
#guests-list table tr {
			height: 32px
}
#guests-list table tr.active, #guests-list table tr.details {
	background-color: var(--color-background-dark);
}
#guests-list table .sort-desc::after {
			content: '▼';
			position: absolute;
			right: 10px;
}
#guests-list table .sort-asc::after {
			content: '▲';
			position: absolute;
			right: 10px;
}
#guests-list table th {
			border-bottom: 1px #ddd solid;
			cursor: pointer;
}
#guests-list table th .sort_arrow {
				float: right;
				color: #888;
}
#guests-list table td:first-child, #guests-list table th:first-child {
	padding-left: 30px;
}
#guests-list table td:last-child, #guests-list table th:last-child {
	padding-right: 30px;
	text-align: right;
}
#guests-list table td, #guests-list table th {
	padding: 10px;
	position: relative;
	display: table-cell;
}
#guests-list table td.groups, #guests-list table th.groups {
	width: 400px;
}
#guests-list table td.remove, #guests-list table th.remove {
	width: 32px;
}
#guests-list table td a.icon, #guests-list table th a.icon {
				margin-left: 5px;
}
